.vetskitchen-testimonials__item {
  padding: 50px 0;
  margin: 0 auto;
  max-width: 866px;
}
.vetskitchen-testimonials__item .image {
  display: block;
  margin: 0 auto 40px;
}
.vetskitchen-testimonials__item .quote-icon {
  margin-bottom: 40px;
  text-align: center;
}
.vetskitchen-testimonials__item .quote-icon svg {
  width: 32px;
  height: auto;
}
.vetskitchen-testimonials__item .content {
  padding: 0 20px;
  font-size: 20px;
  line-height: 1.2;
}
.vetskitchen-testimonials__item .content .name {
  margin-top: 30px;
  font-weight: 700;
  color: #42459d;
  text-align: center;
}
.vetskitchen-testimonials__item + .vetskitchen-testimonials__item {
  border-top: 5px solid #ddd;
}